import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import { VFG } from "../../common/icons/icons";
import { Offcanvas } from "react-bootstrap";
import { useState } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";

function Menu({ allServers, changeCompany }) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <Navbar bg="dark" variant="dark">
      <Container>
        <div className="mt-2 mb-2">
          <span onClick={handleShow}>
            <VFG />
          </span>

          <Offcanvas show={show} onHide={handleClose}>
            <Offcanvas.Header closeButton>
              <Offcanvas.Title>Menu</Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <DropdownButton id="dropdown-basic-button" title="Clientes">
                <Dropdown.Item onClick={() => changeCompany("FERRERE")}>
                  FERRERE
                </Dropdown.Item>
                <Dropdown.Item onClick={() => changeCompany("DWG")}>
                  DWG
                </Dropdown.Item>
              </DropdownButton>
            </Offcanvas.Body>
          </Offcanvas>
          {allServers ? (
            <span className="ms-5">Total de servidores: {allServers}</span>
          ) : null}
        </div>
        <Navbar.Toggle />
        <Navbar.Collapse className="justify-content-end">
          {/* <Navbar.Text>
            Conectado como: <a href="#login">Valentn lvarez</a>
          </Navbar.Text> */}
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Menu;
