import React, { useState } from "react";
import { Table } from "react-bootstrap";
import Modals from "../../../common/modals/modal";
import Delete from "../../../common/actions/delete";
import { collection, addDoc } from "firebase/firestore";
import db from "../../../services/firebaseConfig";
import Swal from "sweetalert2";

function Task({ id, name, tasksData }) {
  const tasks = tasksData;
  const [ticket, setTicket] = useState("");
  const [issue, setIssue] = useState("");

  const [task, setTask] = useState("");
  const [tech, setTech] = useState("");

  const date = new Date().toLocaleDateString("es-ar");
  const now = new Date().toLocaleTimeString("es-ar");
  const render = tasks.length >= 1;

  const handlerTicket = (event) => {
    setTicket(event.target.value);
  };
  const handlerIssue = (event) => {
    setIssue(event.target.value);
  };
  const handlerTask = (event) => {
    setTask(event.target.value);
  };
  const handlerTech = (event) => {
    setTech(event.target.value);
  };

  const cleanInputs = () => {
    setTicket("");
    setIssue("");
    setTask("");
    setTech("");
  };

  const saveData = async () => {
    await addDoc(collection(db, "tasks"), {
      idPrimary: id,
      server_name: name,
      task: task,
      ticket: ticket,
      date: date,
      issue: issue,
      tech: tech,
      hour: now,
    });
    cleanInputs();
    Swal.fire({
      position: "top-end",
      icon: "success",
      title: "Creado con exito!",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  return (
    <>
      <div className="container card mt-3">
        <Modals
          typeModal={"sm-down"}
          btn={
            <div className=" position-relative mb-2">
              <div className="btn">
                <span className="material-symbols-outlined position-absolute top-0 end-0">
                  add_circle
                </span>
              </div>
            </div>
          }
          title={"Generar intervencion"}
          body={
            <>
              <p>
                Detalle la tarea sobre el servidor: <strong>{`${name}`}</strong>
              </p>
              <p>
                Fecha: <strong>{date}</strong>
              </p>
              <input
                className="form-control mb-3"
                placeholder="Link de Ticket asociado"
                type="text"
                onChange={handlerTicket}
                value={ticket}
              />
              <input
                className="form-control mb-3"
                placeholder="Asunto"
                type="text"
                onChange={handlerIssue}
                value={issue}
              />
              <input
                className="form-control mb-3"
                placeholder="Tecnico"
                type="text"
                onChange={handlerTech}
                value={tech}
              />
              <textarea
                className="border form-control"
                placeholder="Escribe aqui las tareas realizadas..."
                onChange={handlerTask}
                value={task}
                cols="60"
                rows="10"
              ></textarea>
            </>
          }
          btn_save={true}
          fun_save={saveData}
          btn_close={"Cerrar"}
        />
        <h3>Tareas</h3>
        <Table className="table ">
          <thead>
            <tr>
              <th>Asunto</th>
              <th>Tecnico</th>
            </tr>
          </thead>

          {render ? (
            tasks.map(
              ({ id, tech, date, issue, serverName, ticket, hour, task }) => (
                <tbody key={id}>
                  <tr>
                    <td>{issue}</td>
                    <td>{tech}</td>
                    <td>
                      <Modals
                        btn={
                          <>
                            <div className="btn inline">
                              <span className="material-symbols-outlined ">
                                visibility
                              </span>
                            </div>
                            <div className="btn inline"></div>
                          </>
                        }
                        title={
                          <>
                            Tarea realizada
                            <Delete id={id} name={issue} collection={"tasks"} />
                          </>
                        }
                        body={
                          <>
                            <div className="card mb-2">
                              <p className="mb-5">
                                <strong>Ticket:</strong>{" "}
                                {ticket ? (
                                  <a className="text-primary" href={ticket}>
                                    {ticket}
                                  </a>
                                ) : (
                                  <p>No hay ticket asociado</p>
                                )}
                              </p>
                            </div>
                            <div className="card mb-2">
                              <p className="mb-5">
                                <strong>Asunto:</strong> {issue}
                              </p>
                            </div>
                            <div className="card mb-2">
                              <p className="mb-5">
                                <strong>Fecha:</strong> {date}
                                <div className="mt-2">
                                  <strong>Hora:</strong> {hour}
                                </div>
                              </p>
                            </div>
                            <div className="card mb-2">
                              <p className="mb-5">
                                <strong>Tarea realizada:</strong>
                                <div>{task}</div>
                              </p>
                            </div>
                          </>
                        }
                        btn_close={"Cerrar"}
                      />
                    </td>
                  </tr>
                </tbody>
              )
            )
          ) : (
            <tbody>
              <tr>
                <td>No hay datos</td>
                <td>No hay datos</td>
              </tr>
            </tbody>
          )}
        </Table>
      </div>
    </>
  );
}

export default Task;
