import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { addDoc, collection } from "firebase/firestore";
import db from "../../../services/firebaseConfig";
import Swal from "sweetalert2";
import { Modal } from "react-bootstrap";

function New() {
  const saveData = async (values, { resetForm }) => {
    setLgShow(false);
    await addDoc(collection(db, "servers"), {
      company: values.company,
      provider: values.provider,
      contact_provider: values.contact_provider,
      name: values.name,
      so: values.so,
      ip: values.ip,
      cpu: values.cpu,
      ram: values.ram,
      description: values.description,
      notes: values.notes,
    });
    resetForm();
    Swal.fire({
      position: "top-end",
      icon: "success",
      title: "Creado con exito!",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const validate = (values) => {
    const errors = {};
    if (values.name.length < 1)
      errors.name = "El nombre es un campo obligatorio!";
    if (values.description.length < 1)
      errors.description = "La descripcin es un campo obligatorio!";
    if (values.company.length < 1)
      errors.company = "Empresa es un campo obligatorio!";
    return errors;
  };

  const [lgShow, setLgShow] = useState(false);

  return (
    <>
      <div className="btn">
        <span
          onClick={() => setLgShow(true)}
          className="material-symbols-outlined "
        >
          add_circle
        </span>
      </div>
      <Modal
        size="lg"
        show={lgShow}
        onHide={() => setLgShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-lg">
            Crear nuevo servidor
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Formik
            initialValues={{
              company: "",
              provider: "",
              contact_provider: "",
              name: "",
              so: "",
              ip: "",
              cpu: "",
              ram: "",
              description: "",
              notes: "",
            }}
            onSubmit={saveData}
            validate={validate}
          >
            <Form>
              <table className="new table">
                <tbody key={1}>
                  <tr>
                    <td className="fs-6 fw-bold">Empresa</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Empresa"
                        name="company"
                        type="text"
                        autocomplete="off"
                      />
                      <div className="text-danger">
                        <ErrorMessage name="company" />
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Proveedor</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Proveedor"
                        name="provider"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Contacto</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Contacto del proveedor"
                        name="contact_provider"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Nombre</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Nombre del servidor"
                        name="name"
                        type="text"
                        autocomplete="off"
                      />
                      <div className="text-danger">
                        <ErrorMessage name="name" />
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Sistema Operativo</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Sistema Operativo"
                        name="so"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">IP</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="IP"
                        name="ip"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">CPU</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="CPU"
                        name="cpu"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">RAM</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="RAM"
                        name="ram"
                        type="text"
                        autocomplete="off"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Descripcin</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Describe la funcion de este servidor"
                        name="description"
                        as="textarea"
                        autocomplete="off"
                      />
                      <div className="text-danger">
                        <ErrorMessage name="description" />
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Notas</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Notas de interes"
                        name="notes"
                        as="textarea"
                        autocomplete="off"
                      />
                      <button
                        className="btn btn-secondary mt-3 me-3"
                        type="button"
                        onClick={() => setLgShow(false)}
                      >
                        Cerrar
                      </button>
                      <button className="btn btn-primary mt-3" type="submit">
                        Guardar
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </Form>
          </Formik>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default New;
