import React, { useState } from "react";
import db from "../../../services/firebaseConfig";
import { addDoc, doc, collection, updateDoc } from "firebase/firestore";
import Modals from "../../../common/modals/modal";
import { Table } from "react-bootstrap";
import Delete from "../../../common/actions/delete";
import Swal from "sweetalert2";

function RelationWith({ id, name, relation }) {
  const relations = relation;

  const [serverRelation, setServerRelation] = useState("");
  const [descritpion, setDescritpion] = useState("");

  const [idRef, setId] = useState("");

  const render = relation.length >= 1;

  const handlerServer = (event) => {
    setServerRelation(event.target.value);
  };

  const handlerDescription = (event) => {
    setDescritpion(event.target.value);
  };

  const cleanInputs = () => {
    setServerRelation("");
    setDescritpion("");
  };

  const saveData = async () => {
    try {
      await addDoc(collection(db, "relations"), {
        idPrimary: id,
        serverName: name,
        serverRelation: serverRelation,
        description: descritpion,
      });
      cleanInputs();
      Swal.fire({
        position: "top-end",
        icon: "success",
        title: "Guardado con exito!",
        showConfirmButton: false,
        timer: 1500,
      });
    } catch (error) {
      console.error(error);
    }
  };

  const updateData = async () => {
    const docRef = doc(db, "relations", idRef);

    await updateDoc(docRef, {
      description: descritpion,
    });
    Swal.fire({
      position: "top-end",
      icon: "success",
      title: "El item fue actualizado con exito!",
      showConfirmButton: false,
      timer: 1500,
    });
  };

  return (
    <>
      <div className="container card mt-3">
        <Modals
          typeModal={"sm-down"}
          btn={
            <div className="position-relative mb-2">
              <div className="btn">
                <span
                  onClick={cleanInputs}
                  className="btns material-symbols-outlined position-absolute top-0 end-0"
                >
                  add_circle
                </span>
              </div>
            </div>
          }
          title={"Agregar relacin"}
          body={
            <>
              <div className="input-group mb-3">
                <span className="input-group-text  fw-bold">{name}</span>
                <span className="input-group-text">&rArr;</span>
                <input
                  type="text"
                  onChange={handlerServer}
                  className="form-control"
                  value={serverRelation}
                />
              </div>

              <textarea
                className="border form-control"
                placeholder="De que manera estan relacionados?"
                onChange={handlerDescription}
                value={descritpion}
                cols="60"
                rows="10"
              ></textarea>
            </>
          }
          btn_save={true}
          fun_save={saveData}
          btn_close={"Cerrar"}
        />
        <h3>Relaciones</h3>
        <Table className="table">
          <thead>
            <tr>
              <th>Servidor</th>
              <th></th>
              <th>Relacionado</th>
            </tr>
          </thead>

          {render ? (
            relations.map(({ id, serverName, serverRelation, description }) => (
              <tbody key={id}>
                <tr>
                  <td>{serverName}</td>
                  <td>&rArr;</td>
                  <td>{serverRelation}</td>

                  <td className="table-relation">
                    <Modals
                      btn={
                        <>
                          <span className="btn">
                            <span className="material-symbols-outlined btns inline">
                              visibility
                            </span>
                          </span>
                        </>
                      }
                      title={
                        <>
                          <Modals
                            typeModal={"sm-down"}
                            btn={
                              <span
                                onClick={() => {
                                  setDescritpion(description);
                                  setId(id);
                                }}
                                className="btns material-symbols-outlined inline"
                              >
                                edit
                              </span>
                            }
                            title={"Editar relacin"}
                            body={
                              <>
                                <div className="input-group mb-3">
                                  <span className="input-group-text  fw-bold">
                                    {name}
                                  </span>
                                  <span className="input-group-text">
                                    &rArr;
                                  </span>
                                  <span className="input-group-text fw-bold">
                                    {serverRelation}
                                  </span>
                                </div>

                                <textarea
                                  className="border form-control"
                                  placeholder="De que manera estan relacionados?"
                                  onChange={handlerDescription}
                                  value={descritpion}
                                  cols="60"
                                  rows="10"
                                ></textarea>
                              </>
                            }
                            btn_save={true}
                            fun_save={updateData}
                            btn_close={"Cerrar"}
                          />
                          <Delete
                            id={id}
                            name={serverRelation}
                            collection={"relations"}
                          />
                        </>
                      }
                      body={
                        <>
                          <h5>
                            {serverName} &rArr; {serverRelation}
                          </h5>
                          <h4>Relacin:</h4>
                          <div className="card">
                            <p className="fs-5">{description}</p>
                          </div>
                        </>
                      }
                    />
                  </td>
                </tr>
              </tbody>
            ))
          ) : (
            <tbody>
              <tr>
                <td>No hay datos</td>
                <td></td>
                <td>No hay datos</td>
              </tr>
            </tbody>
          )}
        </Table>
      </div>
    </>
  );
}

export default RelationWith;
