import React, { useState } from "react";
import { updateDoc, doc } from "firebase/firestore";
import db from "../../../services/firebaseConfig";
import { Formik, Form, Field } from "formik";
import Swal from "sweetalert2";
import { Modal } from "react-bootstrap";

function Edit({
  id,
  name,
  company,
  provider,
  contact_provider,
  so,
  ip,
  cpu,
  ram,
  notes,
  description,
  data,
}) {
  const updateData = async (values) => {
    const docRef = doc(db, "servers", id);
    await updateDoc(docRef, {
      company: values.company,
      provider: values.provider,
      contact_provider: values.contact_provider,
      name: values.name,
      so: values.so,
      ip: values.ip,
      cpu: values.cpu,
      ram: values.ram,
      description: values.description,
      notes: values.notes,
    });
    data();
    setLgShow(false);
    Swal.fire({
      position: "top-end",
      icon: "success",
      title: "Editado con exito!",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const [lgShow, setLgShow] = useState(false);

  return (
    <>
      <div className="btn">
        <span
          onClick={() => setLgShow(true)}
          className="material-symbols-outlined"
        >
          edit
        </span>
      </div>
      <Modal
        size="lg"
        show={lgShow}
        onHide={() => setLgShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-lg">
            {`Editar servidor ${name}`}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Formik
            initialValues={{
              company: company ? company : "",
              provider: provider ? provider : "",
              contact_provider: contact_provider ? contact_provider : "",
              name: name ? name : "",
              so: so ? so : "",
              ip: ip ? ip : "",
              cpu: cpu ? cpu : "",
              ram: ram ? ram : "",
              description: description ? description : "",
              notes: notes ? notes : "",
            }}
            onSubmit={updateData}
          >
            <Form>
              <table className="new table">
                <tbody key={1}>
                  <tr>
                    <td className="fs-6 fw-bold">Empresa</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Empresa"
                        name="company"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Proveedor</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Proveedor"
                        name="provider"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Contacto</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Contacto del proveedor"
                        name="contact_provider"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Nombre</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Nombre del servidor"
                        name="name"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Sistema Operativo</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Sistema Operativo"
                        name="so"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">IP</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="IP"
                        name="ip"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">CPU</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="CPU"
                        name="cpu"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">RAM</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="RAM"
                        name="ram"
                        type="text"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Descripcion</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Describe la funcion de este servidor"
                        name="description"
                        as="textarea"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td className="fs-6 fw-bold">Notas</td>
                    <td>
                      <Field
                        className="form-control"
                        placeholder="Notas de interes"
                        name="notes"
                        as="textarea"
                      />
                      <button
                        className="btn btn-secondary mt-3 me-3"
                        type="button"
                      >
                        Cerrar
                      </button>
                      <button className="btn btn-primary mt-3" type="submit">
                        Guardar
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </Form>
          </Formik>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default Edit;
