import React, { useState } from "react";
import { Table } from "react-bootstrap";
import New from "./actions/new";
import { Link } from "react-router-dom";

function Servers({
  allServers,
  serverPagination,
  currentPage,
  prevHandler,
  nextHandler,
}) {
  const allServer = allServers;
  const serversPagination = serverPagination;
  const [search, setSearch] = useState("");
  const [finder, setFinder] = useState("");
  const render = allServers.length >= 1;

  const searcher = (e) => {
    setSearch(e.target.value);
  };

  const result = () => {
    if (!search) {
      return serversPagination;
    } else if (finder === "description") {
      return allServer.filter((e) =>
        e.description.toLowerCase().includes(search.toLocaleLowerCase())
      );
    } else if (finder === "provider") {
      return allServer.filter((e) =>
        e.provider.toLowerCase().includes(search.toLocaleLowerCase())
      );
    } else if (finder === "notes") {
      return allServer.filter((e) =>
        e.notes.toLowerCase().includes(search.toLocaleLowerCase())
      );
    } else {
      return allServer.filter((e) =>
        e.name.toLowerCase().includes(search.toLocaleLowerCase())
      );
    }
  };

  return (
    <>
      <div className="position-relative container mt-3">
        <div className="position-absolute top-0 end-0">
          <New />
        </div>
      </div>
      <div className="container mt-5">
        <select
          value={finder}
          onChange={(event) => setFinder(event.target.value)}
          className="form-select size-select select "
        >
          <option value="name">Nombre</option>
          <option value="provider">Proveedor</option>
          <option value="description">Descripcion</option>
          <option value="notes">Notas</option>
        </select>
        <input
          className="container form-control w-50 mb-3 mt-3"
          value={search}
          onChange={searcher}
          type="text"
          placeholder="Selecciona un filtro"
        />
        <Table responsive="lg">
          <thead>
            <tr>
              <th>Empresa</th>
              <th>Proveedor</th>
              <th>Nombre</th>
              <th>Sistema Operativo</th>
              <th>IP</th>
              <th>CPU</th>
              <th>RAM</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {render ? (
              result().map(
                ({ name, company, provider, cpu, ram, ip, so, id }) => (
                  <tr key={id}>
                    <td>{company ? company : "No hay datos registados"}</td>
                    <td>{provider ? provider : "No hay datos registrados"}</td>
                    <td>{name ? name : "No hay datos registrados"}</td>
                    <td>{so ? so : "No hay datos registrados"}</td>
                    <td>{ip ? ip : "No hay datos registrados"}</td>
                    <td>{cpu ? cpu : "No hay datos registrados"}</td>
                    <td>{ram ? ram : "No hay datos registrados"} GB</td>
                    <td>
                      <Link to={`/server/details/${id}`}>
                        <span className="material-symbols-outlined btns">
                          visibility
                        </span>
                      </Link>
                    </td>
                  </tr>
                )
              )
            ) : (
              <tr>
                <td>No hay datos</td>
                <td>No hay datos</td>
                <td>No hay datos</td>
                <td>No hay datos</td>
                <td>No hay datos</td>
                <td>No hay datos</td>
                <td>No hay datos</td>
              </tr>
            )}
          </tbody>
        </Table>
      </div>
      <ul className="pagination mt-3 center">
        <li className="">
          <p className="page card me-2 btn" onClick={prevHandler}>
            Anterior
          </p>
        </li>
        <li className="page-item">
          <p className="btn card">{currentPage}</p>
        </li>
        <li className="">
          <p className="card ms-2 btn" onClick={nextHandler}>
            Siguiente
          </p>
        </li>
      </ul>
    </>
  );
}

export default Servers;
